     <router-link to=\"/login\" class=\"btn btn-link\">\r\n                            <el-button type=\"primary\">Войти</el-button>\r\n                        </router-link>\r\n                    </div>\r\n                </div>\r\n            </el-col>\r\n        </el-row>\r\n\r\n        <el-dialog title=\"Авторизация\" :visible.sync=\"isAuthWindow\">\r\n            <el-input placeholder=\"username\" v-model=\"authenticateRequest.username\"></el-input>\r\n            <el-input placeholder=\"password\" v-model=\"authenticateRequest.password\" show-password style=\"padding-top:10px\"></el-input>\r\n\r\n            <span slot=\"footer\" class=\"dialog-footer\">\r\n                <el-button type=\"primary\" @click=\"auth()\">Войти</el-button>\r\n            </span>\r\n        </el-dialog>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n    export default {\r\n        name: 'rheader',\r\n        data() {\r\n            return {\r\n                user: [],\r\n                isAuthWindow: false,\r\n                authenticateRequest: {\r\n                    username: '',\r\n                    password: ''\r\n                }\r\n\r\n            }\r\n        },\r\n        computed: {\r\n\r\n        },\r\n        methods: {\r\n            auth: function () {\r\n                var self = this;\r\n                this.$axios.post('users/authenticate', this.authenticateRequest).then(function (response) {\r\n                    console.log(response.data);\r\n                    if (response.data.token) {\r\n                        localStorage.setItem('user', JSON.stringify(response.data));\r\n                        self.user = response.data;\r\n                        self.isAuthWindow = false;\r\n                    }\r\n                    return response.data;\r\n                }).catch(function (error) {\r\n                    console.log(error);\r\n                });\r\n            },\r\n            logout: function() {\r\n                localStorage.removeItem('user');\r\n                this.user = [];\r\n            }\r\n        },\r\n        created() {\r\n            let user = JSON.parse(localStorage.getItem('user'));\r\n\r\n            if (user && user.token) {\r\n                this.user = user;\r\n                return {\r\n                    'Authorization': 'Bearer ' + user.token\r\n                };\r\n            } else {\r\n                return {};\r\n            }\r\n        },\r\n        mounted() {\r\n            \r\n        }\r\n    }\r\n</script>\r\n\r\n<style>\r\n    .el-row {\r\n        margin-bottom: 20px;\r\n        &:last-child\r\n\r\n    {\r\n        margin-bottom: 0;\r\n    }\r\n\r\n    }\r\n\r\n    .el-col {\r\n        border-radius: 4px;\r\n    }\r\n\r\n    .bg-purple-dark {\r\n        background: #99a9bf;\r\n    }\r\n\r\n    .bg-purple {\r\n        background: #d3dce6;\r\n    }\r\n\r\n    .bg-purple-light {\r\n        background: #e5e9f2;\r\n    }\r\n\r\n    .grid-content {\r\n        border-radius: 4px;\r\n        min-height: 36px;\r\n    }\r\n\r\n    .row-bg {\r\n        padding: 10px 0;\r\n        background-color: #f9fafc;\r\n    }\r\n\r\n    .header {\r\n        position: fixed;\r\n        width: 100%;\r\n        height: 100px;\r\n        z-index:1000;\r\n        background-color:white;\r\n    }\r\n</style>"],"sourceRoot":""}]);
